* {
    box-sizing: boarder-box;
}

body {
    margin: 0;
}

#home-name {
  color: white;
}
.container {
    margin: 1rem;
    text-align: center;
}
.nav {
    background-color: #293241;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
}

.site-title{
    font-size: 2rem;
}
.nav ul{
    padding: 0%;
    margin: 0%;
    list-style: none;
    display: flex;
    gap: 1rem;
}
.nav a{
    color: #a2a8d3;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;

}

.nav li:active{
    background-color: #3D5A80;
}

.nav li:hover{
    background-color: #38598b;
}
